.cat-info {
  display: flex;
}

.image {
  margin-right: 10px;
}

.loader {
  color: #ff3d00;
  letter-spacing: 2px;
  width: 100%;
  height: 10px;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  font-size: 30px;
  font-weight: bold;
  animation: animloader 2s linear infinite;
  display: block;
  position: relative;
}

.loader:after {
  content: "Loading...";
  color: #0a0a0a;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  animation: animloader 2s ease-in infinite;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.is-hidden {
  display: none;
}

.breed-select {
  margin-bottom: 20px;
}

.name {
  border: 2px solid #0a0a0a;
  border-radius: 8px;
  padding-left: 8px;
  padding-right: 8px;
  display: inline-block;
}

.breed-info, .temperament {
  max-width: 400px;
  border: 2px solid #0a0a0a;
  border-radius: 8px;
  padding-left: 8px;
  padding-right: 8px;
}

